import org.w3c.dom.Node
import kotlinx.browser.document
import kotlinx.browser.window
import org.w3c.dom.HTMLTextAreaElement

fun main() {
    window.onload = { init() }

}

fun init() {
    document.querySelector("#check")?.addEventListener("click", {
        check()
    })
}

fun check() {
    val text1 = (document.querySelector(".left textarea") as? HTMLTextAreaElement)?.value ?: return
    val text2 = (document.querySelector(".right textarea") as? HTMLTextAreaElement)?.value ?: return
    val result1 = document.querySelector(".left .result") ?: return
    val result2 = document.querySelector(".right .result") ?: return
    val array1 = text1.split("\n").filter { it.isNotBlank() }.distinct()
    val set1 = array1.toHashSet()
    val array2 = text2.split("\n").filter { it.isNotBlank() }.distinct()
    val set2 = array2.toHashSet()
    result1.innerHTML =
        array1.filter { !set2.contains(it) }.joinToString("\n") { it }
    result2.innerHTML =
        array2.filter { !set1.contains(it) }.joinToString("\n") { it }
}